.home {
    background-color: $color-light;
    h2 {
        text-align: center;
        padding: 1rem 0;
        font-size: $font-size-h5;
    }

    &__cardbox {
        margin: 2rem auto;
        width: 80%;
    }

    &__card {
        flex: 1;
        margin: 0 4px;
        text-align: center;

        h3 { 
            font-size: $font-size-h4;
            margin: 0 0 1.5rem 0;
        }

        img {
            margin: 8px 0;
        }
    }

    &__carousel {
        max-width: 600px;
        margin: 2.4rem auto;
        position: relative;
        display: flex;
    }

}

.carousel__image--1,
.carousel__image--2,
.carousel__image--3 {
    transition: opacity .3s ease-in-out;
    opacity: 0;
}

.carousel__image--2 {
    transform: translateX(-100%);
}

.carousel__image--3 {
    transform: translateX(-200%);
}

.carousel__image--active {
    opacity: 1;
}


.carousel__arrow--right,
.carousel__arrow--left {
    margin: 0 auto;
    height: 2rem; 
    width: 2rem;
    border-style: solid;
    border-color: $color-light;
    border-width: 0px 3px 3px 0px;
    border-radius: 4px;
    transition: all 0.2s ease-in-out;
    cursor: pointer;
    z-index: 5;
}

.carousel__arrow--left {
    position: absolute;
    top: 50%;
    left: 1rem;
    transform: rotate(135deg);
}

.carousel__arrow--right {
    position: absolute;
    top: 50%;
    right: 1rem;
    transform: rotate(-45deg);
}

@media screen and (min-width: $bp-tablet) {
    .home h2 {
        font-size: $font-size-h4;
    }
    .home__cardbox {
        width: 100%;
        display: flex;
        justify-content: space-between;
    }

    .home__card:not(:last-child) {
        margin-right: .5rem;
    }
}

@media screen and (min-width: $bp-desktop) {
    .home__carousel {
        max-width: 800px;
    }

    .home__card:not(:last-child) {
        margin-right: 2.5rem;
    }
    .home__card h3 {
        font-size: $font-size-h2;
    }

}